.f-text{
    text-align: center;
    font-size: 24px;
    color: #eaeaea;
}
.f-text  i {
    animation: heartbeat 1s infinite;
}

@keyframes heartbeat{
    0%{
        transform: scale(1);
    }
    50%{
        transform: scale(1.25);
    }
    100%{
        transform: scale(1);
    }
}

@media screen and (max-width:769px){
    .f-text{
        font-size: 15px;
    }
}