.sec-1{
    display: flex;
    justify-content:flex-end;
    padding: 30px;
    align-items: center;
}
.top-btn{
    font-size: 2.4rem;
    width: 4rem;
    height: 4rem;
    color: #f56539;
    background-color: rgb(33, 33, 33);
    border: 6px solid #f56539;
    border-radius: 50%;
    position: fixed;
    bottom: 5rem;
    z-index: 999;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}
.top-btn > i{
    font-size: 2rem;
    animation: gototop 1.2s linear infinite alternate-reverse;
}
@keyframes gototop{
    0%{
        transform: translateY(-0.4rem);
    }
    100%{
        transform: translateY(0.4rem);
    }
}
@media screen and (max-width:769px){
    .sec-1{
        padding: 8px;
    }
    .top-btn{
        width: 2.5rem;
        height: 2.5rem;
        border: 4px solid #f56539;
    }
    .top-btn > i{
        font-size: 1rem;
    }
}

