.c-main{
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    padding: 30px 30px 50px 30px;
}
.c-text{
    display: flex;
    flex-direction: column;
    font-size: 50px;
    color: #f56539;
    width: 40%;
}
.c-head{
    display: flex;
    flex-direction: row;
    text-align: center;
    margin-top: 36px;
}
.c-head > h1{
    font-family: 'Cinzel', serif;
    padding-top: 50px;
}
.c-icon > a > i{
    padding: 24px;
    color: #f56539;
}
.c-img{
     width: 100%;
}
.c-icon > a > i:hover{
    color:#eaeaea ;
    transform: scale(1.2);
    transition: 0.3s ease-in-out;
}
.input-container{
    width: 350px;
    position: relative;
}
.form{
    width: 26%;
    margin: 40px;
}
.f-label{
    position: absolute;
    left: 46px;
    top: 10px;
    transition: all 0.2s;
    padding: 0 2px;
    z-index: 1;
    color: #f56539;
    font-weight: 700;
}
.f-input{
    padding: 0.8rem;
    width: 100%;
    height: 100%;
    border: 3px solid #f56539;
    background-color: rgb(33, 33, 33);;
    border-radius: 25px;
    font-size: 18px;
    outline: none;
    transition: all .3s;
    color: #eaeaea;
    margin: 20px;
}
.f-label::before{
    content: "";
    height: 5px;
    background-color: rgb(33, 33, 33);;
    position: absolute;
    left: 2px;
    top: 10px;
    width: 100%;
    z-index: -1;
}
.f-input{
    border: 4px solid #f56539;
    top: -10px;
}
.f-textarea{
    height: 150px;
}
.s-btn{
    text-align: center;
}
.s-send{
    width: 80px;
    height: 40px;
    font-weight: 400;
    color: rgb(33,33 ,33);
    background-color: #f56539;
    border: none;
    border-radius: 25px;
    letter-spacing: 2px;
}
.s-send:hover{
    color: #eaeaea;
    border: 2px solid #eaeaea;
    background-color: rgb(33, 33, 33);;
    transform: scale(0.9);
    transition: 0.3s ease-in-out;
    cursor: pointer;
}
@media screen and (max-width:769px){
    .c-main{
        display: flex;
        flex-direction: column;
        align-items: center;
        padding: 0px 30px 0px 30px;
    }
    .c-head{
        display: flex;
        flex-direction: column;
        justify-content: center;
    }
    .c-text{
        display: flex;
        flex-direction: column;
        font-size: 20px;
        color: #f56539;
        width: 85%;
        text-align: center;
        text-decoration: underline;
    }
    .c-icon > a > i{
        padding: 8px;
        font-size: 25px;
    }
    .c-head > h1{
        padding: 8px 0px;
        margin-bottom: 5px;
    }
    .input-container{
        width: 85%;
        position: relative;
    }
    .form{
        width: 90%;
    }
}